<template>
  <section class="page-demo">
     <ptheader class="rpublicity_header">个人信息</ptheader>
    <mt-cell
      title="医德医风档案信息"
      to="/accounts/medical"
      is-link
      value="">
    </mt-cell>
    <mt-cell
      title="个人简历"
      to="/accounts/resume"
      is-link
      value=""></mt-cell>

    <mt-cell
      title="历年奖励情况"
      to="/accounts/awards"
      is-link
      value=""></mt-cell>


    <mt-cell
      title="历年受惩情况"
      to="/accounts/punish"
      is-link
      value=""></mt-cell>





    <footer @click="loginout">
      注销
    </footer>


  </section>
</template>
<script>
  import ptheader from "@/components/header/ptheader";
  import {MessageBox} from 'mint-ui';
  export default  {
    data(){
       return {
         account: '',
         recordCreateTime: '',
         orgUnitId: '',
         name: '',
         sex: '',
         nation: '',
         partisan: '',
         birthday: '',
         jobRank: '',
         jobDuties: '',
         oeducation: '',
         startWorkTime: '',
         comeUnitTime: '',
         isAssessor: '',
         assessUnitId: '',
         userProperty: '',
         jobLevel: '',
         identification: '',
         resume: '',
         reward: '',
         punish: ''
       }
    },
    components:{
      ptheader
    },
    methods:{
      loginout() {
        this.$http.post("/logout").then(res => {
          MessageBox.confirm('确认退出登录?').then(res => {
            var _this = this;
            localStorage.setItem("ccc",null);
            _this.$router.push(_this.$route.query.redirect || '/signin')
          }).catch(action => {
          })
        });

      }
    }
  }
</script>
<style scoped>
 ul li{
   height:4rem;
   text-indent: 1rem;
   line-height: 4rem;
   border-bottom: 1px solid #f7f7f7;

 }
 ul li input{
   border: 1px solid red;
   width: 20rem;
   height: 3rem;
 }

 footer{
   width: 100%;
   height: 3rem;
   color: white;
   letter-spacing: 0.5rem;
   background: #0b752b;
   line-height: 3rem;
   text-align: center;
   position: fixed;
   bottom: 0px;
   font-size: 16px;
 }
</style>



